import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Platform } from "@ionic/angular";
import { map, switchMap, tap } from "rxjs/operators";
import { environment } from "src/environments/environment";
import { File } from "@ionic-native/file/ngx";
import { FileTransfer } from "@ionic-native/file-transfer/ngx";
import { Storage } from "@ionic/storage";

const LKEY = "mylicences";

@Injectable({
  providedIn: "root",
})
export class ContentService {
  public path;
  public platf;
  public openedContent;
  public openedSong;
  constructor(
    private http: HttpClient,
    private platform: Platform,
    private file: File,
    private transfer: FileTransfer,
    private storage: Storage
  ) {
    this.definePath();
  }
  definePath() {
    let pathh;
    if (
      (this.platform.is("desktop") && !this.platform.is("electron")) ||
      this.platform.is("mobileweb") ||
      this.platform.is("pwa")
    ) {
      pathh = "";
    } else if (this.platform.is("electron")) {
      const { app } = (<any>window).require("electron").remote;
      const path = (<any>window).require("path");
      pathh = path.join("file:///", app.getPath("userData"), "contents/");
    } else {
      pathh = this.file.externalApplicationStorageDirectory + "contents/";
    }
    this.path = pathh;
  }
  getJson(filename, cp) {
    let content = this.openedContent;
    let pathh = content.download_location;
    if (this.path !== "") pathh = this.path + content.dirname + "/";
    if (this.path !== "" && !this.platform.is("electron")) pathh = cp;
    return this.http.get(pathh + btoa(content.dirname + "/" + filename));
  }
  validateCode(code) {
    return this.http
      .post<any[]>(`${environment.resourceurl}/validatecode/` + code, {})
      .pipe(
        map((data) => {
          return data;
        })
      );
  }
  syncLicences() {
    return this.http
      .post<any[]>(`${environment.resourceurl}/mylicences`, {})
      .pipe(
        map((data) => {
          return this.storeLicence(data);
        })
      );
  }
  storeLicence(newLicence) {
    if (newLicence.status !== 200) return;
    newLicence = newLicence.data;
    let mylicences;
    let addedLicences = [];
    this.getStoredLicence().then((data) => {
      mylicences = data;
      if (newLicence.length >= 1) {
        newLicence.forEach((e) => {
          e.downloaded = false;
          if (mylicences.length >= 1) {
            if (mylicences.some((el) => el.id == e.id)) return;
            mylicences.push(e);
            addedLicences.push(e);
          } else {
            mylicences.push(e);
            addedLicences.push(e);
          }
        });
      }
      if (addedLicences.length > 0) {
        this.storage.set(LKEY, mylicences);
      }
    });
    return addedLicences;
  }
  async getStoredLicence() {
    let result;
    await this.storage.get(LKEY).then((data) => {
      if (!data) data = [];
      result = data;
    });
    return result;
  }
  async checkDirExist(content) {
    let result;
    if (this.platform.is("electron")) {
      const { app } = (<any>window).require("electron").remote;
      const path = (<any>window).require("path");
      const fs = (<any>window).require("fs");
      let pathh = path.join(app.getPath("userData"), "contents");
      let pathh1 = path.join(
        app.getPath("userData"),
        "contents",
        content.dirname
      );
      if (!fs.existsSync(pathh)) {
        fs.mkdirSync(pathh);
      }
      if (!fs.existsSync(pathh1)) {
        fs.mkdirSync(pathh1);
      }
      result = pathh1;
    } else {
      await this.file
        .checkDir(this.file.externalApplicationStorageDirectory, "contents")
        .then(() => {
          this.file
            .checkDir(
              this.file.externalApplicationStorageDirectory,
              "contents/" + content.dirname
            )
            .catch(() => {
              this.file.createDir(
                this.file.externalApplicationStorageDirectory,
                "contents/" + content.dirname,
                false
              );
            });
        })
        .catch(() => {
          this.file
            .createDir(
              this.file.externalApplicationStorageDirectory,
              "contents",
              false
            )
            .then(() => {
              this.file
                .checkDir(
                  this.file.externalApplicationStorageDirectory + "contents/",
                  content.dirname
                )
                .catch(() => {
                  this.file.createDir(
                    this.file.externalApplicationStorageDirectory + "contents/",
                    content.dirname,
                    false
                  );
                });
            });
        });
      result =
        this.file.externalApplicationStorageDirectory +
        "contents/" +
        content.dirname;
    }
    return result;
  }
  async checkFileList(content) {
    let result = [];
    await this.checkDirExist(content).then((folder) => {
      if (this.platform.is("electron")) {
        const { app } = (<any>window).require("electron").remote;
        const path = (<any>window).require("path");
        const fs = (<any>window).require("fs");
        let pathh = path.join(
          app.getPath("userData"),
          "contents",
          content.dirname
        );
        result = fs.readdirSync(pathh);
      } else {
        this.file
          .listDir(
            this.file.externalApplicationStorageDirectory + "contents/",
            content.dirname
          )
          .then((data) => {
            let files = [];
            data.forEach((e) => {
              if (e.isDirectory) return;
              files.push(e.name);
            });
            result = files;
          })
          .catch((err) => {
            console.log(err);
          });
      }
    });
    return await result;
  }
  checkResourceList(content) {
    return this.http.post<any[]>(
      `${environment.resourceurl}/getcontentlist/${content.dirname}`,
      {}
    );
  }
  async compareContent(content) {
    let resources = await this.checkResourceList(content).toPromise();
    let local = await this.checkFileList(content);
    let listToDownload = [];
    if (resources) {
      listToDownload = await resources["data"].filter(function (obj) {
        return local.indexOf(obj) == -1;
      });
    }
    return listToDownload;
  }
  // async startDownloading(content) {

  // }
  downloadFileElectron(configuration) {
    const fs = (<any>window).require("fs");
    const request = (<any>window).require("request");
    return new Promise(function (resolve, reject) {
      var received_bytes = 0;
      var total_bytes = 0;
      var req = request({
        method: "GET",
        uri: configuration.remoteFile,
      });
      var out = fs.createWriteStream(configuration.localFile);
      req.pipe(out);
      req.on("response", function (data) {
        total_bytes = parseInt(data.headers["content-length"]);
      });
      if (configuration.hasOwnProperty("onProgress")) {
        req.on("data", function (chunk) {
          received_bytes += chunk.length;
          configuration.onProgress(received_bytes, total_bytes);
        });
      } else {
        req.on("data", function (chunk) {
          received_bytes += chunk.length;
        });
      }
      req.on("end", function () {
        resolve();
      });
    });
  }
  setDownloadedStatus(content, status) {
    this.getStoredLicence().then((data) => {
      if (data) {
        let licences = data.map((licence) => {
          return licence.id == content.id
            ? { ...licence, downloaded: status }
            : licence;
        });
        this.storage.set(LKEY, licences);
      }
    });
  }
  async emptyLicence() {
    this.storage.remove(LKEY);
  }
  deleteDownloadedContent(content) {
    if (this.platform.is("electron")) {
      const { app } = (<any>window).require("electron").remote;
      const path = (<any>window).require("path");
      const fs = (<any>window).require("fs");
      let pathh = path.join(
        app.getPath("userData"),
        "contents",
        content.dirname
      );
      fs.rmdirSync(pathh, { recursive: true });
      this.setDownloadedStatus(content, false);
    } else {
      let pathh = this.file.externalApplicationStorageDirectory + "/contents/";
      this.file.removeRecursively(pathh, content.dirname);
      this.setDownloadedStatus(content, false);
    }
  }
  async deleteContentFromStored(content) {
    await this.deleteDownloadedContent(content);
    await this.getStoredLicence().then((data) => {
      if (data) {
        let licences = data.filter((el) => {
          return el.id != content.id;
        });
        this.storage.set(LKEY, licences);
      }
    });
  }
  hoverSound() {
    let audio = new Audio();
    audio.src = "assets/audio/hover.mp3";
    audio.load();
    audio.play();
  }
  clickSound() {
    let audio = new Audio();
    audio.src = "assets/audio/click.mp3";
    audio.load();
    audio.play();
  }
}
